@mixin position(
  $position,
  $top: null,
  $right: null,
  $bottom: null,
  $left: null
) {
  position: $position;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
}

@mixin flex($direction: null, $justify: null, $align: null) {
  display: flex;

  flex-direction: $direction;

  justify-content: $justify;

  align-items: $align;
}

@mixin horizontal-center {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
}

